.c-cardProduct {
    @include opacity(1);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    height: 100%;
    &__figure {
        flex: 0 1 auto;
        width: 100%;
        position: relative;
        overflow: hidden;
        display: block;
        .figureHero {
            display: block;
            width: 100%;
            img {
                display: block;
                width: 100%;
                height: 430px;
                object-fit: cover;
                object-position: center;
            }
        }
    }
    &__sizes {
        @include transition();
        @include opacity(0);
        overflow: hidden;
        position: absolute;
        bottom: -40px;
        left: 0;
        display: flex;
        justify-content: center;
        width: 100%;
        background: rgba(0,0,0,0.3);
        z-index: 2;
        padding: 15px 5px;
        li {
            padding: 0 2px;
        }
        a {
            @include transition();
            @include roundedAll(16px);
            display: flex;
            width: 30px;
            height: 30px;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            background: $white;
            font-size: 13px;
            font-weight: $fwRegular;
            color: $black;
            margin: 0 auto;
            border: 1px solid transparent;
            &:hover {
                background: $white3;
                border: 1px solid $primary;
                color: $primaryLight;
            }
        }
    }
    &__content {
        align-self: flex-start;
        display: flex;
        flex: 1 1 auto;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        padding: 20px;
        .content__head {
            @include flexWidth(100%);
            @include paragrafos($tc: $black, $fw: $fwBold, $mbLOT: 20px);
            text-align: center;
            margin-bottom: 20px;
            align-self: flex-start;
        }
        .content__end {
            @include flexWidth(100%);
            flex: 0 1 auto;
            align-self: flex-end;
            margin-top: auto;
        }
        .price__old {
            @include paragrafos($fs: 14px, $mb: 0);
            display: block;
            text-decoration: line-through;
            text-align: center;
        }
        .price__now {
            @include paragrafos($fw: $fwBold, $fs: 18px, $mb: 0);
            display: block;
            padding: 2px 0 5px 0;
            text-align: center;
        }
        .price__credit {
            @include paragrafos($fs: 14px, $mb: 0);
            display: block;
            text-align: center;
        }
        .c-btn {
            @include opacity(0);
            max-width: 100%;
            margin-top: 10px;
        }
    }
    &:hover {
        background: $white;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.15);
        .c-cardProduct__sizes {
            @include opacity(1);
            bottom: 0;
        }
        .c-cardProduct__content {
            .c-btn {
                @include opacity(1);
            }
        }
    }
}
